import {useState} from 'react'
import { Worker } from '@react-pdf-viewer/core';
import { Viewer } from '@react-pdf-viewer/core';
import '@react-pdf-viewer/core/lib/styles/index.css';
// import { defaultLayoutPlugin } from '@react-pdf-viewer/default-layout';

// import '@react-pdf-viewer/default-layout/lib/styles/index.css';

import "./App.css"
import Navbar from './Components/Navbar';


function App() {

  const [pdfFile, setPdfFile]=useState(null);
//   const defaultLayoutPluginInstance = defaultLayoutPlugin();

  const [pdfError, setPdfError]=useState('');
  const allowedFiles = ['application/pdf'];
  const handleFile = (e: React.ChangeEvent<HTMLInputElement>) =>{
    if (!e.target.files) return;
    let selectedFile = e.target.files[0];
    if(selectedFile){
      if(selectedFile&&allowedFiles.includes(selectedFile.type)){
        
        let reader = new FileReader();
        reader.readAsDataURL(selectedFile);
        reader.onloadend=(e: any)=>{
          setPdfError('');
          setPdfFile(e.target.result);
        }
      }
      else{
        setPdfError('Not a valid pdf: Please select only PDF');
        setPdfFile(null);
      }
    }
    else{
      console.log('please select a PDF');
    }
  }


  const selectedText = () => {
    
            const selected = window.getSelection() as Selection
            if (selected.toString()) {
              alert(selected.toString())
            }  else {
              return null
            } 
}


  return (
    
    <div onMouseUp={selectedText}  className="container">
<Navbar />
      <form>
        <label><h5>Upload PDF</h5></label>
        <br></br>

        <input type='file' className="form-control"
        onChange={handleFile}></input>
        {pdfError&&<span className='text-danger'>{pdfError}</span>}

      </form>
      <h5>View PDF</h5>
      <div className="viewer">
        {pdfFile&&(
          <Worker workerUrl="https://unpkg.com/pdfjs-dist@3.6.172/legacy/build/pdf.worker.min.js">
            
            {/* <Viewer plugins={[defaultLayoutPluginInstance]}  fileUrl={pdfFile}></Viewer> */}
            <Viewer  fileUrl={pdfFile}></Viewer>
          </Worker>
        )}
        {!pdfFile&&<>No file is selected yet</>}

      </div>


    </div>
  );
}

export default App;